<style scope lang="less">
  .article-item {
    margin-top: 40px;
    .article-mess {
     display: flex;
      justify-content: center;
      color: rgba(31,31,31,.5);
      font-weight: 100;
      font-size: 14px;
    .date-line {

    }
    .eye:before {
